*{
  box-sizing: border-box;
padding:0;
margin:0;
}

html,body {
    margin:0;
    padding:0;
}

.Navbar{
  width: auto;
height:150px;
background:white;

}

.Navbar ul{
display:flex;
height:100%;
list-style:none;
padding-top:1rem;
padding-bottom:1rem;
margin-right:3rem;
margin-left:3rem;
}

.Navbar ul li{
  
  margin-right:1rem;
  margin-left:1rem;
}


.Navbar ul button{
  
border:none;
background:none;
margin-top:10px;
width:100px;

&:hover{
border-bottom:5px solid black;
}

&:focus{
border-bottom:5px solid black;
}
}

.Navbar ul li p{
font-size:13px;
margin-top:5px;
font-weight: 600;
text-align:center;
}