.slider{
overflow: hidden;
box-sizing: border-box;
width: 1080;
height: 500px;
background-color: black;
display: flex;
}

.slider_btn{
box-sizing: border-box;
width: 40px;
height: 40px;
margin-top: auto;
margin-bottom: auto;
padding: 0px;
border: 0;
background: none;
}

.slider_btn_image{
width: 40px;
height: 40px;
background: rgba(180, 180, 180, 0.3);
margin: none;
border: 0;
border-radius: 800px;
}

.slider_btn_image:hover{
background: rgba(255, 255, 255, 0.8);
}

.images{
box-sizing: border-box;
display: block;
margin-left: 0;
margin-right: 0;
width: 100%;
height: 100%;
transition: transform .8s; /* Animation */
}

.images:hover{
box-sizing: border-box;
transform: scale(1.05);
}

.productDescription{
position: relative;
top:45%;
width: 50%;
text-align: right;
color: white;
}

.productHeader{
position: relative;
text-align: right;
font-size: 35px;
font-weight: 700;
}