.exploreContainer{
display: flex;
width:100%;
height: 700px;
background:none;
padding-left:3rem;
padding-right:3rem;
border-radius:20px;
}

.header{
width:100%;
margin-left:auto;
margin-right:auto;
text-align:center;
padding:3rem;
transition: 0.4s;

&:hover{
  color:white;
}
}

.image{
width:55%;
transition:0.8s;
overflow:hidden;

&:hover{
transform:scale(1.05);
}
}

.querryButtons ol{
box-sizing: border-box;
list-style-font: 20px;
position:relative;
margin-top:150px;
margin-left:10px;
left: 20px;
padding:0;
}



.querryButtons ol li h1{
width:490px;
height:80px;
font-size:22px;
font-weight:400;
transition:0.3s;


&:hover{
border-top:3px solid black;
border-bottom:1px solid gray;
font-weight:700;
}

&:hover .image{
border-top:3px solid black;
border-bottom:1px solid gray;
font-weight:700;
}
}