nav{
box-sizing: border-box;
background: rgba(220, 220, 250, 0.6);
display: flex;
color: black;
justify-content: space-between;
padding: 1rem 10rem;
}

.companyLogo{
width: 10rem;
height: 85%;
}

nav ul{
margin-right: 30rem;
margin-top:auto;
margin-bottom: auto;
list-style: none;
display: flex;
}

nav ul li{
box-sizing: border-box;
padding-right: 1rem;
padding-left: 1rem;
border-radius: 20px;

&:hover{
background-color: black;
color: white;
}
}

nav a{
color: inherit;
text-decoration: none;
font-size: 15px;
font-weight: 500;
}