.footer{
width:inherit;
margin:3rem;
border-top:1px solid black;
margin-right: 3rem;
margin-left: 3rem;

}

.footer p{
font-size:12px;
width:50%;
}

.footer .upper{
padding-bottom:20px;
border-bottom:1px solid black;
}

.footer .lower{
display:flex;
}

.footer .lower ul{
display:flex;
margin-left:-42%;
border-left:1px solid black;
}

.footer .lower ul li button{
background:none;
border:none;
position:relative;
margin-right:3rem;
&:hover{
color:red;
}
}