.body{
box-sizing: borde-box;
width:100%;
height:auto;
padding-left: 3rem;
padding-right: 3rem;
}

ul li{
list-style:none;
padding: 0;
margin:0;
}

ul li div{
width:100%;
height:750px;
border-radius:20px;

}

.mobile{
background-size: 100%;
background-repeat: no-repeat;
transition: 0.8s;
}
.tv{
background-size: 100%;
background-repeat: no-repeat;
transition: 0.8s;
position:relative;
}
.homeApps{
background-size: 100%;
background-repeat: no-repeat;
transition: 0.8s;
position:relative;
}

.header{
text-align:center;
padding: 30px;
margin-left: auto;
margin-right: auto;
color:white;
font-weight:500;
mix-blend-mode: difference;
}

.mobileLinks{
color:white;
display:flex;
list-style:none;
text-align:center;
margin:0;
padding:0;
}

.mobileLinks li{
margin-left:auto;
margin-right:auto;
}

.mobileLinks li button{
color: inherit;
background:none;
border:none;
transition: 0.8s;
font-size:18px;
mix-blend-mode: difference;

&:hover
{
color:gray
}

&:focus{
border-bottom:2px;
border-bottom-color:white;
box-shadow: 0px 3px 1px white;
}
}

.tvLinks{
color:white;
display:flex;
list-style:none;
text-align:center;
margin-left:20%;
margin-right:20%;
}

.tvLinks li{
margin-left:auto;
margin-right:auto;
}

.tvLinks li button{
color: inherit;
background:none;
border:none;
transition: 0.8s;
font-size:18px;
mix-blend-mode: difference;

&:hover
{
color:gray
}

&:focus{
box-shadow: 0px 3px 1px white;
}
}
