.headers{
  width: 100%;
  white-space: nowrap;
  margin:3rem;
  text-align: center;
  transition: .8s;
  
  &:hover{
    color: white;
  }
}

.subheaders{
  width: 100%;
  margin-left: 1rem;
  margin-right: 1rem;
  text-align: center;
  transition: .8s;
  
  &:hover{
    color: white;
  }
}


.imageSlider{
  width: inherit;
  height: 380px;
  padding-left: 4rem;
  padding-right: 4rem;
  background-size: 100%;
  background-repeat: no-repeat;
  transition: 0.8s;
}

.imageSlider .leftbtn{
  width:2.5rem;
  height: 2.5rem;
  background: none;
  border: none;
  position:relative;
  top:10rem;
  left:1rem;
  
  &:hover{
    -webkit-filter: invert(100%);
    filter: invert(100%);
  }
}

.imageSlider .rightbtn{
  width:2.5rem;
  height: 2.5rem;
  background: none;
  border: none;
  position:relative;
  top:10rem;
  left:86rem;
  
  &:hover{
    -webkit-filter: invert(100%);
    filter: invert(100%);
  }
}

.imageSlider h1{
  mix-blend-mode: difference;
  color:white;
  margin-left:55rem;
  margin-top:4rem;
  transition: 0.8s;
}
.imageSlider p{
  mix-blend-mode: difference;
  color:white;
  margin-left:55rem;
  font-weight: 300;
  transition: 0.8s;
}
.imageSlider .buynow{
  mix-blend-mode: difference;
  width:5rem;
  background: white;
  border: none;
  color: black;
  height: 2rem;
  border-radius: 20px;
  margin-top: 1rem;
  margin-left: 60rem;
}

.showcase{
  width: inherit;
  margin-right: 8rem;
  margin-left: 8rem;
  transition: 0.8s;
}

.showcase ul{
  list-style:none;
  display:flex;
}

.showcase ul li button{
  font-weight: 700;
  font-size:16px;
  width:130px;
  transition: .3s;
  
  background: none;
  border: none;
  
  &:hover{
    color: white;
  }
  &:focus{border-bottom: 4px solid black;
    color: white;}
}

.showcasegrid{
  display:grid;
  margin-top: 2rem;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 25px;
}

.griditem{
  box-sizing: border-box;
  width:320px;
  height:400px;
  background: white;
  border-radius:30px;
  text-align:center;
}

.griditem h1{
  height: 2rem;
  margin-left: 2rem;
  margin-right: 2rem;
  margin-top: 2rem;
  font-size:14px;
  text-align: left;
}

.griditem p{
  text-align:left;
  margin-top: 2rem;
  margin-left: 2rem;
  margin-right: 2rem;
  font-weight: 300;
  font-size: 15px;
}

.buynow{
  width: 80px;
  height: 30px;
  background: black;
  color:white;
  border-radius: 20px;
  border: none;
}

.griditem img{
  width:200px;
  height: auto;
  transition:transform .8s;
/*   -webkit-filter: invert(100%); */
}

.colorbtn{
  width: 20px;
  height: 20px;
  border-radius: 200px;
  border:none;
  margin:5px;
  
  
  &:focus{
    border:1px solid black;
  }
}

.gridseries{
  display: flex;
  grid-gap: 20px;
}

.gridseries img{
  width:inherit;
  height: auto;
}

.gridseries h1{
  font-size: 24px;
  font-weight: 700;
}
.gridseries .buynow{
  width: 80px;
  height: 30px;
  margin-top: 4rem;
  background: black;
  color:white;
  border-radius: 20px;
  border: none;
}

.conditions{
  margin:8rem;
  font-size:18px;
  font-weight: 300;
}

.conditions p{
  margin-top: 1rem;
}

.video-responsive {
  overflow: hidden;
  position: relative;
  height: 0;
}

.video-responsive iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: relative;
}

.exceptionH{
  position: relative;
  right: 35rem;
}